import React from 'react'

export default class ListaDeLocaisCadastrados extends React.Component {
    constructor(){
        super()
    
        this.state = {
            cursoId : undefined,
            listaDeCursos : [],
            listaDeLocais : undefined,
        }

        this.carregaCursos = this.carregaCursos.bind(this)
        this.carregaListaDeLocais = this.carregaListaDeLocais.bind(this)
    }

    componentWillMount(){
        this.carregaCursos()
    }

    carregaCursos() {
        fetch(`https://api-estagil-gm-dev.herokuapp.com/cursos`)
        .then(response => {
            if(response.ok)
                response.json().then(listaDeCursos => this.setState({listaDeCursos}))
            // else
            //     response.text().then(Promise.reject.bind(Promisse))
        })
        .catch(erros => console.log(erros))
    }

    carregaListaDeLocais(cursoId){
        fetch(`https://api-estagil-gm-dev.herokuapp.com/locais_de_estagio/${cursoId}/curso_id`)
        .then(response => {
            if(response.ok)
                response.json().then(listaDeLocais => this.setState({listaDeLocais}))
            // else
            //     response.text().then(Promise.reject.bind(Promisse))
        })
        .catch(erros => console.log(erros))
    }

    render(){
        const {cursoId, listaDeLocais, listaDeCursos} = this.state

        const listaDeLocaisCarregada = listaDeLocais !== undefined
        const listaDeLocaisVazia = listaDeLocaisCarregada && listaDeLocais.length === 0
        const listaDeLocaisComResultado = listaDeLocaisCarregada && listaDeLocais.length > 0

        if(cursoId === undefined){
            return(
                <div className="form-group">
                    <label className="badge badge-light" htmlFor="curso">Passo 1: Selecione seu curso</label>
                    <select 
                        className="custom-select"
                        value={cursoId}
                        onChange={ (e) => {
                            const cursoId = e.target.value
                            this.setState({cursoId})
                            this.carregaListaDeLocais(cursoId)
                        }}
                        required
                    >
                        {
                            (listaDeCursos === undefined || listaDeCursos.length === 0) ?
                                <option key='' value=''>Aguarde, carregando lista de cursos...</option>    
                            :   
                                <option key='false' value=''>Por favor, selecione aqui o seu curso</option>
                        }
                        {
                            listaDeCursos.map(curso => <option key={curso.id} value={curso.id}>{curso.nome}</option>)
                        }
                    </select>
                </div>
            )
        }

        // Lista de locais retornou com resultados
        if(listaDeLocaisComResultado){
            return (
                <div>
                    <a className="badge badge-primary mr-1" href="#lista_locais" onClick={() => this.setState({cursoId:undefined,listaDeLocais:undefined})} children="Voltar"/>
                    <strong>Campos de estágio cadastrados para o curso {listaDeCursos.filter(curso=> curso.id === cursoId)[0].nome}</strong>
                    <table className="table table-striped table-hover">
                        <thead>
                            <th>Nome</th>
                            <th>Cidade</th>
                        </thead>
                        <tbody>
                        {
                            listaDeLocais.map(local => (
                                <tr>
                                    <td>{local.nome}</td>
                                    <td>{local.endereco.cidade}</td>
                                </tr>
                            ))
                        }
                        </tbody>
                    </table>
                </div>
            )
        }

        // Lista de locais retornou sem resultados
        else if(listaDeLocaisVazia){
            return (
                <div>
                    <a className="badge badge-primary mr-1" href="#lista_locais" onClick={() => this.setState({cursoId:undefined,listaDeLocais:undefined})} children="Voltar"/>
                    <strong>Não localizamos nenhum local de estágio cadastrado para o curso {listaDeCursos.filter(curso=> curso.id === cursoId)[0].nome}</strong>
                    <p>Gostaria de propor algum campo de estágio? Clique <a className="badge badge-info" href="#proposta_concedente">aqui</a> e envie os dados do local para o NURI.<br/>
                    Não esqueça de colocar nas observações da proposta alguma informação para entrarmos em contato contigo e te informar sobre o resultado desta parceria ;)</p>
                </div>
            )
        }

        // Lista de locais ainda sem resultados
        return (
            <div>
                <p>Aguarde</p>
            </div>
        )
        
    }
}