import React from 'react'

export default function BarraDeNavegacao(props){
    const {logo} = props
    return (
        <div className="pl-1 bg-dark container">
            <label className="badge badge-light mr-1 mt-1 mb-1">
                <img src={logo} height="30" alt="IF Baiano"/>        
                Estágios GM
            </label>
            <LinkButton className="btn-success" location="#">Página Inicial</LinkButton>
            <LinkButton className="btn-warning" location="#requerimento">Requerimento</LinkButton>
            <LinkButton className="btn-primary" location="#lista_locais">Lista de Locais Cadastrados</LinkButton>
            <LinkButton className="btn-secondary" location="#proposta_concedente">Propor Local para Estágios</LinkButton>
        </div>
    )
}

function LinkButton(props){
    const {location, className, children, value} = props
    
    return (
        <button {...props} className={"btn mr-1 " + className} onClick={()=>window.location = location}>
            {value === undefined ? children : value}
        </button>
    )
}